import { Outlet, Link } from "react-router-dom";
import './css/movil.css'

const  Barra = () =>
{
    return(
        <div>     
          <nav class="navbar navbar-expand-lg bg-body-tertiary bg-dark" data-bs-theme="dark">
            <div class="container-fluid">
              <button class="navbar-toggler" type="button" data-bs-toggle="collapse" 
              data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" 
              aria-expanded="false" aria-label="Toggle navigation">
                <span class="navbar-toggler-icon"></span>
              </button>
              <div class="collapse navbar-collapse" id="navbarSupportedContent">
                <ul class="navbar-nav me-auto mb-2 mb-lg-0 text-center">
                <li className="nav-item">
                    <Link className="nav-link text-light fs-5" aria-current="page" to="/">Inicio</Link>
                  </li>
                  <li class="nav-item">
                    <Link className="nav-link text-light fs-5" to="/about">Sobre mi</Link>
                  </li>
                  <li class="nav-item">
                    <Link className="nav-link text-light fs-5" to="/contacto">Contacto</Link>
                </li>
                </ul>
                <span className="text-light fs-3">
                  <b className="parrafo">Bienvenido a mi portafolio personal</b>
                </span>
              </div>
            </div>
          </nav>
          <Outlet />
        </div>
    );
}

export default Barra;